import React from 'react';
import styled from 'styled-components';
import  { quotation3, quotation4, check } from '../common/imgUrls/incruitBuddyImgs';

const EligibilityStyled = styled.section`
  background: ${props => props.colors.grayBack};
  padding-top: 5rem;
  text-align: center;
  width: 100%;

  .eligibility__title {
    background:
      url(${quotation3}) left top/50px no-repeat,
      url(${quotation4}) right bottom/50px no-repeat;
    margin: 0 auto;
    margin-bottom: 4rem;
    text-align: center;
    width: 630px;

    p {
      font-size: 20px;
      padding-top: .5rem;
    }
  }

  .eligibility__details {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    padding-bottom: 5rem;
    width: 25%;

    li {
      background: url(${check}) left/20px no-repeat;
      margin-bottom: .5rem;
      padding-left: 2rem;
      text-align: left;
    }
  }
`;

const eligibilityDetails = [
  {
    id: 1,
    descript: ' 20  , ,  ',
  },
  {
    id: 2,
    descript: ', ,    ',
  },
  {
    id: 3,
    descript: ',    ',
  },
];

const BuddyEligibility = ({ colors }) => {
  return (
    <EligibilityStyled colors={colors}>
      {/* <!--     --> */}
      <h2 className="nonVisible"> </h2>
      <div className="eligibility__title">
        <h3> </h3>
        <p>
          ,      !
        </p>
      </div>
      <ul className="eligibility__details">
        {eligibilityDetails.map(v => {
          return(
            <li key={v.id}>{v.descript}</li>
          )
        })}
      </ul>
      {/* <!--     --> */}
    </EligibilityStyled>
  );
};

export default BuddyEligibility;