import React, { useContext } from 'react';
import styled from 'styled-components';
import * as urls from '../common/imgUrls/servicesImgs';
import { ColorContext } from 'contexts/ColorContext';

const IntrVisionStyled = styled.main`
  align-items: center;
  background: url(${urls.bgDogWalking}) top/auto 820px no-repeat;
  display: flex;
  flex-direction: column;
  min-height: 820px;
  text-align: center;
  width: 100%;

  .services__vision__detail {
    color: ${props => props.colors.white};
    width: 60%;

    strong {
      color: inherit;
      font-size: 100px;
      font-weight: 600;
      line-height: 1.25em;
      padding: 7rem 0 3rem;
      white-space: pre-line;
    }

    p {
      color: inherit;
      font-size: 20px;
      padding-bottom: 4rem;
      white-space: pre-line;
      word-break: keep-all;
    }
  }

  button {
    background: ${props => props.colors.white};
    border: 1px solid ${props => props.colors.greenDark};
    border-radius: 30px;
    color: ${props => props.colors.greenDark};
    font-size: 24px;
    padding: .4rem 2.5rem;

    &:hover {
      background: ${props => props.colors.greenDark};
      color: ${props => props.colors.white};
    }
  }
`;

const IntroServicesVision = () => {
  const colors = useContext(ColorContext);

  return (
    <IntrVisionStyled
      colors={colors}
    >
      {/*     */}
      <h2 className="nonVisible"> </h2>
      <div className="services__vision__detail">
        <strong>
          Love Pet,
          <br />
          Live Together!
        </strong>
        <p>
                 ,          .
          <br /><br />
                    ,                   
        </p>
      </div>
      <button type="button">  </button>
      {/*     */}
    </IntrVisionStyled>
  );
};

export default IntroServicesVision;