import React from 'react';
import styled from 'styled-components';
import * as urls from '../common/imgUrls/incruitBuddyImgs';

const BenefitsStyled = styled.section`
  margin: 0 auto;
  padding-top: 8rem;
  width: 630px;

  .benefits__title {
    background:
      url(${urls.quotation1}) left top/50px no-repeat,
      url(${urls.quotation2}) right bottom/50px no-repeat;
    margin-bottom: 4rem;
    text-align: center;
    width: 100%;
    
    p {
      font-size: 20px;
      padding-top: .5rem;
    }
  }

  .benefits__details {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    margin-bottom: 5rem;
    width: 100%;

    section {
      padding-bottom: 5rem;
      text-align: center;
      width: 260px;

      h4 {
        font-size: 20px;
        font-weight: 600;
        padding: 100px 0 .5rem;
      }

      span {
        font-size: 16px;
        width: 250px;
        word-break: keep-all;
      }
    
      &:nth-of-type(1) {
        h4 {
          background: url(${urls.revenue}) 50% 10px/80px no-repeat;
        }
      }

      &:nth-of-type(2) {
        h4 {
          background: url(${urls.supervise}) 50% 10px/80px no-repeat;
        }
      }

      &:nth-of-type(3) {
        h4 {
          background: url(${urls.buddyInsurance}) 50% 10px/80px no-repeat;
        }
      }

      &:nth-of-type(4) {
        h4 {
          background: url(${urls.education}) 50% 10px/80px no-repeat;
          padding-bottom: 0;
        }
      }
    }
  }
`;

const benefitsDetail = [
  {
    id: 1,
    eng: 'revenue',
    title: '   ',
    descript: '    ,   !'
  },
  {
    id: 2,
    eng: 'supervise',
    title: '  ',
    descript: '      .'
  },
  {
    id: 3,
    eng: 'buddyInsurance',
    title: '  ',
    descript: '     !'
  },
  {
    id: 4,
    eng: 'education',
    title: '    ',
    descript: ',        .'
  },
];

const BuddyBenefits = () => {
  return (
    <BenefitsStyled>
      {/* <!--     --> */}
      <h2 className="nonVisible">  </h2>
      <div className="benefits__title">
        <h3>  </h3>
        <p>
           ,    !
        </p>
      </div>
      <div className="benefits__details">
        {benefitsDetail.map(v => {
          return(
            <section
              key={v.id}
              className={v.eng}
            >
              <h4
                className={`${v.eng}__title`}
              >
                {v.title}
              </h4>
              <span
                className={`${v.eng}__descript`}
              >
                {v.descript}
              </span>
            </section>
          )
        })}
      </div>
      {/* <!--     --> */}
    </BenefitsStyled>
  );
};

export default BuddyBenefits;