import React from 'react';
import { Link } from 'react-router-dom';
import PromoWrapper from './PromoWrapper';

const Header = () => {
  const openModal = () => {
    document.querySelector('.popPromotion').classList.add('active');
  };

  return (
    <>
      {/* <header className="header" onClick={onClick}> */}
      <header
        className="header fixed"
      >
      {/*   */}
        <h1 className="header__logo">
          <Link
            to="/index"
            title=" "
            className="index"
          >
            <img
              src={require("../../assets/img/img_logo.png")}
              alt=" "
            />
          </Link>
        </h1>
        <nav className="header__gnb">
          <button
            title="  "
            onClick={openModal}
          >
              
          </button>
          <Link
            to="/subServices"
            title=" "
            className="subServices"
          >
             
          </Link>
          <Link
            to="/subHelp"
            title=""
            className="subHelpP"
          >
            
          </Link>
          <Link
            to="/subIncruit"
            title="  "
            className="subIncruit"
          >
              
          </Link>
        </nav>
        {/*   */}
      </header>
      <PromoWrapper
        className="popPromotion"
      />
    </>
  );
};

export default Header;